$color-text-primary: black;
$color-text-secondary: rgba(21, 21, 31, 1);
$color-text-tertiary: #7a7a7a;
$background-color-primary: #fff;
$background-color-secondary: #f1f1f1;
$font-family_1: "Inter";

@mixin phone { 
  @media (min-width: 320px) {
    @content; 
  } 
}


@mixin tablet { 
  @media (min-width: 768px) {
    @content; 
  } 
}

@mixin laptop { 
  @media (min-width: 1024px) {
    @content; 
	} 
}

@mixin  desktop { 
  @media (min-width: 1440px) {
    @content; 
  } 
}



body {
  width: 100%;
  height: 100%;
}

.btn {
  cursor: pointer;
}

.header { 
  background-color: $background-color-primary; 
  height: 64px; 

  &__wrapper { 
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; 

  @include laptop {
      justify-content: normal;
    }
  }

	&__nav {
    @include laptop {
      display: flex;
    }
  }
	
  &__lang {
    margin-left: auto;
  }

  &__sign-up {
    margin-left: auto;
    padding: 16px 24px;
    border-radius: 38px;
    background-color: #f5e341;
    font-family: $font-family_1;
    font-weight: 600;
    font-size: 1em;

    &:hover {
      background: rgba(250, 226, 26, 1);
    }
    &:active {
      background: rgba(234, 203, 19, 1);
    }

    @include tablet {
      margin-left: 8px;
    }
  }

  &__login {
    padding: 14px 21.5px;
    border-radius: 38px;
    border: 1px solid #c1c1c1;
    color: $color-text-primary;
    background-color: $background-color-primary;
    font-family: $font-family_1;
    font-size: 1em;
    font-weight: 600;
    display: flex;
    align-items: center;
    margin-left: 30px;

    &:hover {
      border: 1px solid #0c0c0c;
    }
    &:active {
      border: 0;
      background: rgba(237, 237, 238, 1);
      border: 1px solid rgba(237, 237, 238, 1);
    }
    @include phone {
      display: none;
    }

    @include tablet {
      display: flex;
      margin-left: auto;
    }

    @include laptop {
      margin-left: 30px;
    }
  }

  &__logo {
    font-size: 3em;
  }

  &__burger {
    background-image: url("../img/bg-menu.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 24px;
    height: 16px;
    margin-left: 15px;

    @include laptop {
      display: none;
    }
  }
}

.open {
  .nav {
    &__item {
      &:hover {
        background: rgb(255, 255, 255);
        border-radius: 27px;
        padding: 0 0;
        border-radius: 0;
      }
      &:active {
        background: rgb(255, 255, 255);
        border-top: 27px;
        padding: 0 0;
        border-radius: 0;
      }
    }
    &__link {
      &:hover {
        background: rgb(255, 255, 255);
        border-radius: 27px;
        color: $color-text-secondary;
      }
    }

    &__menu {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      width: 345px;
      height: 422px;
      background: #ffffff;
      z-index: 6;
      border-radius: 12px;

      @include tablet {
        width: 437px;
        height: 471px;
      }
    }
  }
  .language {
    padding: 1px 6px;
    &:hover {
      border-radius: 23px;
      background: rgba(237, 237, 238, 1);
      padding: 1px 6px;
      border: 1px solid #c1c1c1;
      border-radius: 12px;
      margin: 0;
    }
    &:active {
      border-radius: 23px;
      background: rgba(237, 237, 238, 1);
      padding: 1px 6px;
      border: 1px solid #c1c1c1;
      border-radius: 12px;
      margin: 0;
    }
  }

  .sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 70px;
    right: 15px;
  }

  .section {
    height: 100%;
    width: 100%;
    background: #7a7a7a;
    z-index: 2;
  }

  .header {
    &__burger {
      background-image: url(../img/close.svg);
      height: 24px;
      width: 24px;
    }
  }
}

.language {
  padding: 10px 10px;
  background: rgba(255, 255, 255, 1);
  border: none;
  color: $color-text-secondary;
  font-family: $font-family_1;
  font-size: 1em;
  font-weight: 600;
  margin-left: auto;

  &:hover {
    border-radius: 23px;
    background: rgba(237, 237, 238, 1);
    padding: 10px 10px;
  }
  &:active {
    border-radius: 23px;
    background: rgba(237, 237, 238, 1);
    padding: 10px 10px;
  }

  @include laptop {
    display: flex;
    align-items: center;
    gap: 15px;
  }
}
.lang-w {
  font-family: $font-family_1;
  font-weight: 500;
  font-size: 1em;
}

.nav {
  margin-left: 40px;
  padding: 10px 0px;
  font-family: $font-family_1;
  font-weight: 600;
  font-size: 1em;
  color: $color-text-tertiary;
  text-decoration: none;

  &__item {
    &:hover {
      background: rgb(255, 255, 255);
      border-radius: 27px;
      padding: 0px 0px;
    }
  }

  &__link {
    text-decoration: none;
    color: #7a7a7a;
    padding: 10px 12px;
    border-radius: 27px;

    &:hover {
      background: rgba(245, 227, 65, 1);
      padding: 10px 12px;
      border-radius: 27px;
      color: $color-text-secondary;
    }

    &:active {
      background: rgba(234, 203, 19, 1);
      padding: 10px 12px;
      border-radius: 27px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
  }

  @include phone {
    display: none;
  }

  @include laptop {
    display: flex;
  }
}

.dropdown {
  display: none;

  &-content {
    @include laptop {
      display: none;
      position: absolute;
      background-color: $background-color-primary;
      width: 131px;
      z-index: 1;
      border: 1px solid rgba(224, 225, 226, 1);
      border-radius: 8px;
      a {
        color: $color-text-primary;
        padding: 12px 16px;
        text-decoration: none;
        display: block;
        &:hover {
          background-color: $background-color-secondary;
        }
      }
    }
  }

  @include laptop {
    margin-left: auto;
    position: relative;
    display: inline-block;
    &:hover {
      .dropdown-content {
        display: block;
      }
    }
  }
}

.sidebar {
  display: none;
  .nav {
    margin: 0;

    &__link {
      padding-left: 24px;
      font-family: $font-family_1;
      font-weight: 600;
      font-size: 1em;
      color: $color-text-tertiary;
      text-decoration: none;
    }

    &__list {
      display: flex;
      flex-direction: column;
      gap: 0;
    }

    &__item {
      border-bottom: 1px solid #e0e1e2;
      width: 345px;
      line-height: 52px;
      height: 52px;

      @include tablet {
        width: 437px;
        line-height: 64px;
        height: 64px;
      }
    }
  }

  .button {
    display: flex;
    flex-direction: column-reverse;
    gap: 8px;

    @include tablet {
      flex-direction: row-reverse;
      gap: 8px;
      align-items: center;
    }
  }

  .dropdown {
    display: flex;

    &-content {
      display: none;
      position: absolute;
      background-color: $background-color-primary;
      width: 310px;
      z-index: 1;
      border: 1px solid rgba(224, 225, 226, 1);
      border-radius: 8px;
        a {
          color: $color-text-primary;
          padding: 12px 16px;
          text-decoration: none;
          display: block;
          &:hover {
            background-color: $background-color-secondary;
          }
        }
    }
  }

  .language {
    display: flex;
    border-radius: 12px;
    border: 1px solid #c1c1c1;
    margin: 0;
    gap: 255px;
    align-items: center;
    font-family: $font-family_1;
    font-size: 15px;
    font-weight: 600;

    @include tablet {
      gap: 310px;
    }
  }

  .header {
    &__sign-up {
      font-family: $font-family_1;
      font-weight: 600;
      font-size: 1em;
      padding: 16px 100px;
      background: #f5e341;
      border-radius: 38px;

