      font-weight: 600;
      font-size: 1em;
      border: 1px solid #c1c1c1;
      border-radius: 38px;
      margin: 0;
      margin-bottom: 8px;

      @include tablet {
        padding: 20px 54px;
        margin: 0;
      }
    }
  }

  .arrow {
    margin-left: auto;
  }
}

.block {
  padding: 24px;

  @include tablet {
