      line-height: 54px;
    }
  }

  &__text {
    color: #e0e1e2;
    font-weight: 500;

    @include phone {
      font-size: 0.9375em;
      line-height: 25px;
      letter-spacing: -0.015em;
      text-align: left;
      color: rgba(255, 255, 255, 0.6);
      z-index: 3;
    }

    @include tablet {
      font-size: 1.125em;
    }
